<template>
    <div class="main-content">
        <el-page-header @back="goBack" content="查看计划">
        </el-page-header>
        
        
        
        <div class="top-step">
            <el-steps :active="0" style="width:30%;">
              <el-step title="计划设置" description=""></el-step>
              <el-step title="宝贝设置" description=""></el-step>
            </el-steps>
        </div>
        
        
        
        <el-container>
        
        <div style="width: 600px;margin-top: 10px;">
            <el-form ref="form"  label-width="80px">
            <el-form-item label="计划名称">
            <el-input v-model="campaign.title" placeholder="计划名称" maxlength="20"
  show-word-limit></el-input>
            </el-form-item>
            <el-form-item label="开启推广">
                <el-switch
                  v-model="status">
                </el-switch>
            </el-form-item>
            
            <el-form-item label="自动优化">

            <el-switch
              v-model="auto">
            </el-switch>
            
            
            </el-form-item>
            <el-form-item label="每日限额" v-if="campaign.campaign_id">
                
                
                <el-button type="primary" size="medium" plain icon="el-icon-edit" @click="dialogFormVisible=true" >{{budget}}元 </el-button>

                
                <el-dialog title="每日限额" :visible.sync="dialogFormVisible">
                  <div>
                      
                      <!-- <el-switch
                        v-model="budgetLimit"
                        active-text="不设限额"
                        inactive-text="每日限额">
                      </el-switch> -->
                      
                      
                      <el-form-item label="投放预算"><el-input-number v-model="newbudget"></el-input-number> 元</el-form-item> 
                      <el-form-item label="快捷选项">
                          <el-tag @click="newbudget=100"> 100 </el-tag> 
                          <el-tag  type="success" @click="newbudget=200"> 200 </el-tag> 
                          <el-tag type="warning" @click="newbudget=300"> 300 </el-tag>
                          <el-tag type="danger" @click="newbudget=500"> 500 </el-tag>
                      </el-form-item>
                  </div>
                  <div slot="footer" class="dialog-footer">
                    <el-button @click="dialogFormVisible = false">取 消</el-button>
                    <el-button type="primary" @click="saveNewBudget">确 定</el-button>
                  </div>
                </el-dialog>
              
            </el-form-item>
            <el-form-item v-else label="每日限额">
                <el-input v-model="budget"></el-input>(0为不限制，最低30元)
            </el-form-item>
            
            <el-form-item>
                <el-button type="primary" @click="AutoSaveStep1"> 保存 </el-button>
                 <el-button @click="go2next" :disabled="campaign.campaign_id? false: true"> 下一步 </el-button>
            </el-form-item>
            
            </el-form>

        </div>

                
        
        
       
       
       
       
        </el-container>
        

        
        

    </div>
</template>

<script>

    import {request} from "@/network/request.js";
    


    export default {
        data() {
            return {
                campaign_id:0,
                title:"",//创意名称
                auto:false,//自动优化
                status:false,
                budgetLimit:true,//设置每日限额
                budget: 0, //推广预算
                newbudget:0,
                options:[50,100,200],
                campaign:{} ,//信息
                dialogFormVisible:false, //预算设定对话框
                
                count:1,
            }

       },
        components:{
        },
        watch: {
            auto(val) {
                this.campaign.auto =  this.auto ? "1" : "0";
            },
            status(val) {
                this.campaign.online_status =  this.status ? "online" : "offline";
            }
        },
        methods: {
            load () {
                
                if(this.count< 100)
                {
                    console.log(this.count);
                    this.count += 2
                }
                
                    
                    
                    
                    
                  },
            //保存计划设置按钮
            AutoSaveStep1()
            {
                this.campaign.budget=this.budget;
                
                request({
                    url: "/auto/AutoSaveStep1",
                    params:this.campaign,
                }).then(res => {
                    console.log('submit:',res)
                    
                    this.campaign = res.data.data.campaign;
                    this.campaign.budget=this.budget;
                    
                    this.$notify({
                              title: '成功',
                              message: '操作成功，进入下一步',
                              type: 'success'
                            });
                            
                            
                })
                
                setTimeout(()=>{
                    this.go2next();
                },2500)
            },
            //获取推广预算等信息
            getCampaignsBudget()
            {
                request({
                    url: "/auto/getCampaignsBudget",
                    params:this.campaign,
                }).then(res => {
                    console.log('getCampaignsBudget:',res)
                    
                    this.budget = this.newbudget = res.data.campaign_budget.budget;
                    
                    
                })
                
            },
            //保存预算
            saveNewBudget()
            {
                
                if(this.newbudget != this.budget)
                {
                    this.campaign.budget=this.newbudget
                    
                    request({
                        url: "/auto/saveCampaignsBudget",
                        params:this.campaign,
                    }).then(res => {
                        console.log('saveCampaignsBudget:',res)
                        this.budget=res.data.campaign_budget.budget;
                        
                        this.$notify({
                                  title: '成功',
                                  message: '操作成功',
                                  type: 'success'
                                });
                                
                                
                    })
                    
                    
                }
                
                this.dialogFormVisible=false;
                
            },
            go2next()
            {
                this.$router.push({path:"viewCampaignsStep2",query:this.campaign})
            },
            goBack()
            {
                this.$router.back();
            },
                  handleOpen(key, keyPath) {
                    console.log(key, keyPath);
                  },
                  handleClose(key, keyPath) {
                    console.log(key, keyPath);
                  }
            

        },
        mounted() {
            this.campaign = this.$route.query;
            
            this.status = this.campaign.online_status == "online" ? true:false;
            this.auto = this.campaign.auto == "1" ? true:false;
            
            if(this.campaign.campaign_id)
            {
                this.getCampaignsBudget();
            }
            
            

        }
    }
</script>

